import { Notification, ThemeProvider } from "gatsby-interface"
import React, { useEffect, useState } from "react"
import useEmailNotification from "../hooks/email.hook"
import useFormField from "../hooks/field.hook"
import {
  Button,
  Container,
  EmailInput,
  MessageInput,
  NotificationContainer,
  PageHeading,
} from "./contact.styles"

function Contact() {
  // TODO: email validation
  // TODO: message sanitization

  const email = useFormField("")
  const message = useFormField("")
  const sendEmail = useEmailNotification()

  const { isOpenedSnackbar, showNotification } = useSnackbar()
  const onSendClick = () => {
    showNotification()
    sendEmail(email.value, message.value)
  }

  return (
    <Container>
      <PageHeading id="contact">Reach me out</PageHeading>
      <EmailInput
        id="email"
        placeholder="Your email"
        type="email"
        name="email"
        {...email}
      />
      <MessageInput
        id="message"
        placeholder="Your message"
        name="message"
        {...message}
      />
      <Button onClick={onSendClick}>SEND MESSAGE</Button>

      <NotificationContainer>
        <ThemeProvider>
          <Notification
            variant="PRIMARY"
            size="M"
            tone="SUCCESS"
            isOpened={isOpenedSnackbar}
            content="Thank you! Your message has been sent. Please, wait for my reply :)"
          />
        </ThemeProvider>
      </NotificationContainer>
    </Container>
  )
}

function useSnackbar() {
  const [isOpenedSnackbar, setIsOpenedSnackbar] = useState(false)

  useEffect(() => {
    if (isOpenedSnackbar) {
      setTimeout(() => setIsOpenedSnackbar(false), 5000)
    }
  }, [isOpenedSnackbar])

  return { isOpenedSnackbar, showNotification: () => setIsOpenedSnackbar(true) }
}

export default Contact
